import axios from 'axios';
import { message, Button, Input } from 'antd';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router';
import { PUBLIC_URL } from '../../config';
import { updateUser } from '../../_actions/user_action';
import MajorSelect from './MajorSelect';
import SecondMajorSelect from './SecondMajorSelect';
function UserInfo(props) {
  const dispatch = useDispatch();
  //    disabled
  const { email, webMail, nickname, mainMajor, doubleMajor } = useSelector(
    (state) => state.user,
  );
  const [mainMajorList, setMainMajorList] = useState([]);
  const [doubleMajorList, setDoubleMajorList] = useState([]);
  const [change, setChange] = useState({
    nickname: nickname,
    majorId: mainMajor,
    majorId: doubleMajor,
  });
  const [webMailInput, setWebMailInput] = useState(webMail);
  useEffect(async () => {
    await axios
      .all([
        axios.get(`${PUBLIC_URL}/major/main-major`),
        axios.get(`${PUBLIC_URL}/major/double-major`),
      ])
      .then((response) => {
        setMainMajorList(response[0].data.data);
        setDoubleMajorList(response[1].data.data);
      });
  }, []);
  const onSubmit = () => {
    const answer = window.confirm('   ');

    if (answer) {
      dispatch(updateUser(change))
        .then((response) => {
          message.success(' .');
        })
        .catch((error) => {
          switch (error.response?.status) {
            case 400:
              message.error('  30   ');
              break;
            case 401:
              message.error('  ');
              props.history.push('/');
              break;
            case 403:
              message.error('  ');
              break;
            case 409:
              message.error('  .');
              break;
            default:
              break;
          }
        });
    }
  };
  function mainMajorChange(value) {
    setChange({ ...change, majorId: value });
  }
  function doubleMajorChange(value) {
    setChange({ ...change, secondMajorId: value });
  }

  const onAuth = async () => {
    await axios
      .post(`${PUBLIC_URL}/user/email`)
      .then((response) => {
        message.success(
          '  .  ',
        );
      })
      .catch((error) => {
        switch (error?.response?.status) {
          case 401:
            message.error(' .');
            props.history.push('/');
            break;
          default:
            message.error('    ');
            break;
        }
      }); // no parameter?  
  };
  return (
    // google, kakao  .
    <div>
      <div style={{ margin: '8px 0' }}>
        <label></label>
        <div style={{ margin: '8px 0' }}>
          <Input
            value={email}
            style={{ width: '200px' }}
            disabled={true}
          ></Input>
        </div>
      </div>
      <div style={{ margin: '8px 0' }}>
        <label></label>
        <div style={{ margin: '8px 0' }}>
          <Input
            value={webMailInput}
            onChange={(e) => setWebMailInput(e.target.value)}
            style={{ width: '200px' }}
            suffix={<>@hufs.ac.kr</>}
          ></Input>
          <Button onClick={onAuth} style={{ marginLeft: '8px' }}>
            
          </Button>
        </div>

        {/*  ,    disabled   */}
      </div>
      <div style={{ margin: '8px 0' }}>
        <label></label>
      </div>
      <div style={{ margin: '8px 0' }}>
        <Input
          style={{ width: '200px' }}
          type="nickname"
          placeholder={nickname}
          value={change.nickname}
          onChange={(e) => setChange({ ...change, nickname: e.target.value })}
        />
      </div>
      <label></label>
      <MajorSelect
        list={mainMajorList}
        defaultMajor={mainMajor}
        onChange={mainMajorChange}
      />
      <label>/</label>
      <SecondMajorSelect
        list={doubleMajorList}
        defaultSecondMajor={doubleMajor}
        onChange={doubleMajorChange}
      />
      <button onClick={onSubmit}>    </button>
    </div>
  );
}

export default withRouter(UserInfo);
