import axios from 'axios';
import React, { useState } from 'react';
import { Modal, Button, Space, message } from 'antd';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router';
import { withdrawUser } from '../../_actions/user_action';
import { ExclamationCircleOutlined } from '@ant-design/icons';
function UserWithdraw(props) {
  const [visible, setVisible] = useState(false);
  const dispatch = useDispatch();

  function confirm() {
    Modal.confirm({
      title: '  ? ',
      icon: <ExclamationCircleOutlined />,
      content: 'HUFSpace_',
      okText: '',
      cancelText: '',
      onOk() {
        onWithdraw();
      },
    });
  }
  const onWithdraw = () => {
    dispatch(withdrawUser())
      .then((response) => {
        message.success('  .');
        props.history.push('/');
      })
      .catch((error) => {
        switch (error.reponse?.status) {
          case 401:
            message.error(' ');
            props.history.push('/');
          default:
            message.error(error.response?.status);
            break;
        }
      });
  };
  return (
    <div style={{ display: 'inline-block', marginLeft: '40%' }}>
      <div>    , </div>
      <div></div>
      <Button onClick={confirm}>Confirm</Button>
    </div>
  );
}

export default withRouter(UserWithdraw);
